<template>
  <div>
    <v-container class="noticia_container">
      <title-ribbon :title="'Noticias Terapia'" />
      <div
        v-if="render"
        class="noticia_container__main"
      >
        <v-row class="noticia_row__titulo">
          <v-col class="nrt_col">
            <h1>{{ noticia.Titulo }}</h1>
          </v-col>
        </v-row>
        <v-row class="noticia_row__cuerpo">
          <v-col class="nrc_col">
            <div class="nrc_col_main">
              <v-img
                class="noticia-img"
                :src="
                  `https://res.cloudinary.com/dkod5pdlc/image/upload/v${noticia.VersionImagen}/terapiacl/blog/${noticia.Imagen}`
                "
              />
              <div class="float-splitter noticia-splitter" />
              <div class="noticias-datos-autor">
                <p class="fecha">
                  <strong>Fecha de Publicación: </strong>
                  <span v-html="noticia.FechaFormat" />
                </p>
                <p class="por">
                  <strong>Por: </strong><span v-html="noticia.Usuario" />
                </p>
              </div>
              <p
                class="noticia-texto"
                v-html="noticia.Texto"
              />
            </div>
          </v-col>
        </v-row>
        <splitter />
        <v-row class="noticia_row__nosotros">
          <v-col class="nrn_col">
            <div class="nrn_col_main">
              <h2>Nosotros</h2>
              <div class="float-splitter" />
              <p>
                Bienvenidos al Centro de Terapia del Comportamiento, un Centro
                de Salud Mental privado con más de 40 años al servicio de la
                comunidad y que reúne un equipo de Psicólogos, Psiquiatras,
                Neurólogos, Fonoaudiólogos, Psicopedagogos, Nutricionistas y
                Terapeutas Ocupacionales, donde cualquier persona puede acceder
                a servicios especializados de calidad a través de los distintos
                sistemas previsionales y por valores razonables al alcance de la
                mayoría.
              </p>
            </div>
          </v-col>
        </v-row>
        <splitter />
        <v-row class="noticia_row__siguenos">
          <v-col class="nrs_col">
            <siguenos />
          </v-col>
        </v-row>
        <splitter-volver />
      </div>
      <div v-else>
        <v-row>
          <v-col class="text-center">
            <v-progress-circular
              :size="70"
              :width="7"
              color="success"
              indeterminate
            />
          </v-col>
        </v-row>
      </div>
    </v-container>
  </div>
</template>
<script>
  import Vue from 'vue'
  export default {
    name: 'Noticia',
    components: {
      TitleRibbon: () => import('@/components/web/base/TitleRibbon'),
      Splitter: () => import('@/components/web/base/Splitter'),
      SplitterVolver: () => import('@/components/web/base/SplitterVolver'),
      Siguenos: () => import('@/components/web/base/Siguenos'),
    },
    data () {
      return {
        noticia: '',
        render: false,
      }
    },
    created () {
      this.getNoticia()
    },
    methods: {
      getNoticia () {
        Vue.axios
          .get(`/api/noticias/noticia/${this.$route.params.id}`)
          .then(res => {
            this.noticia = res.data.noticia[0]
            this.noticia.FechaFormat = this.noticia.Fecha.substring(
              0,
              this.noticia.Fecha.indexOf('T'),
            )
            this.noticia.Texto = res.data.noticia[0].Texto.replace(
              /(\\r)*\\n/g,
              '',
            )
            this.render = true
          })
          .catch(e => {
            console.log(e.response)
          })
      },
    },
    metaInfo () {
      return {
        title: this.noticia.MetaTitle
          ? `Noticias | ${this.noticia.MetaTitle}`
          : ' ... ',
        titleTemplate: '%s | Centro de Terapia del Comportamiento',
        htmlAttrs: {
          lang: 'es',
          amp: true,
        },
        meta: this.noticia.MetaTags ? this.noticia.MetaTags : [],
      }
    },
  }
</script>
